import React from 'react'
import "react-responsive-carousel/lib/styles/carousel.min.css"; // requires a loader
import { Carousel } from 'react-responsive-carousel';
import { Box, Button, styled, Typography } from '@mui/material';
import { NorthEast } from '@mui/icons-material';
import './style.css'
import { data } from './data';

const Btn=styled(Button)`
color:black;
border:2px solid black;
padding:22px;
font-weight:800;
text-transform:capitalize;
border-radius:0;

`


const Crousel = () => {

  const responsive = {
    desktop: {
      breakpoint: { max: 3000, min: 1024 },
      items: 3,
      slidesToSlide: 3 // optional, default to 1.
    },
    tablet: {
      breakpoint: { max: 1024, min: 464 },
      items: 2,
      slidesToSlide: 2 // optional, default to 1.
    },
    mobile: {
      breakpoint: { max: 464, min: 0 },
      items: 1,
      slidesToSlide: 1 // optional, default to 1.
    }
  };


  return (
      <Carousel
      responsive={responsive}
      dynamicHeight={true}
      autoPlay={true}
      autoplaySpeed={1000}
      customTransition="all .5"
      transitionTime={500}
      infiniteLoop={true}
      >
                  {data.map((ele)=>{
                    return(
                      <>
                      <div style={{display:'flex',justifyContent:'space-around'}}>
                <Box style={{marginTop:'12px'}} >
            <Typography style={{fontSize:'122px'}}>Up To 
                <Typography style={{color:'red',fontSize:'52px'}}>

                50%
                Discount
                </Typography>
                </Typography>
                <Typography style={{fontSize:'32px'}}>{ele.title}</Typography>
                <Typography>New Modern Stylist Fashionable Men's Wear Jeans Shirt.</Typography>
                <Btn endIcon={<NorthEast/>} variant='outlined'>Explore Now</Btn>
        </Box>
        <img src={ele.imgUrl}/>
        </div>
                      </>
                    )
                  })}

                    {/* <p className="legend">Legend 1</p> */}
              
            </Carousel>
  )
}

export default Crousel